@import '../_global';

.hamburger {
    display: none;
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
    right: 2rem;
    flex-direction: column;
    justify-content: space-around;
    width: 3.5rem;
    height: 3.5rem;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    z-index: 20;

    &:focus {
        outline: none;
    }

    div {
        width: 3.5rem;
        height: 0.5rem;
        background: $white;
        border-radius: 1rem;
        transition: all 0.3s linear;
        position: relative;
        transform-origin: 1px;

        // &:nth-child(2) {
 //     margin-left: auto;
 //     width: 3rem;
 // }
    }

    @media screen and (max-width: 850px) {
        & {
            display: flex;
        }
    }
}